







































































	export default {
		data() {
			return {
				tempNum:1
			};
		},
    methods: {
			goPage(e) {
        console.log("e", e.currentTarget.dataset.path)
        let path = e.currentTarget.dataset.path
        this.tempNum = this.tempNum==1?2:1
				uni.navigateTo({
					url: path+"?page=" + this.tempNum
				})
			}
		},
	}
